@import "assets/scss/variables.scss";

.wrapper {
  position: relative;
  height: 50vh;
  overflow: hidden;
  & video {
    margin-bottom: -1rem;
    object-fit: cover;
  }
  & .inner-wrapper {
    position: absolute;
    width: 100%;
    height: calc(100% + 0.5rem);
    background-color: rgba($bg, 0.75);
    display: flex;
    align-items: center;
    & .logo-container {
      display: flex;
      align-items: center;
      flex-direction: column;
      & img {
        height: 5rem;
        margin-bottom: 1rem;
      }
      & h1 {
        font-family: $secondaryFont;
        font-size: $textExtraLarge + 25px;
        letter-spacing: 5px;
        @include sm {
          font-size: $textExtraLarge + 15px;
        }
      }
      & p {
        text-transform: uppercase;
        background-color: $secondary;
        padding: 0.25rem 0.5rem;
        color: $textDark;
        margin: 0.775rem 0;
      }
    }
    & ul {
      margin-top: 1rem;
      @include sm {
        gap: 1rem;
      }
      & li {
        padding-right: 2rem;
        border-right: 1px solid rgba($light, 0.5);
        @include sm {
          padding-right: 1rem;
        }
        &:last-child {
          border: 0;
        }
      }
    }
  }
  & :global(.container-global) {
    @include lg {
      flex-direction: column;
      padding: 0 3.5rem;
    }
    @include sm {
      padding: 0 1.5rem;
      gap: 1.5rem;
    }
  }
}
